<template>
    <div class="selected-item" v-if="selectedItem !== null">
        <div class="header">
            <div class="nav-holder">
              <div class="item-flip" @click="previous" :title="'Previous: ' + previousItem">&lt;</div>
              <h3>{{ selectedItem != null ? selectedItem.title : '' }}</h3>
              <div class="item-flip" @click="next" :title="'Next: ' + nextItem">&gt;</div>
            </div>
            <div class="close" @click="reset" title="Close and return to the Portfolio">X</div>
        </div>
        <div class="body">
            <div class="focus-photo" :style="backgroundImage"></div>
            <div class="photo-carousel">
                <div v-for="(photo, index) in selectedItem.photos" :key="index" class="photo" :style="backgroundImage(photo)" @click.stop="selected = index"></div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
  data: function () {
    return {
      selected: 0
    }
  },
  computed: {
    selectedItem: function () {
      return this.$store.state.selectedItem
    },
    nextItem: function () {
      var selectedId = this.selectedItem.id

      var currentIndex = this.$store.state.newsItems.findIndex(function (el) {
        return el.id === selectedId
      })

      var next = this.$store.state.newsItems[currentIndex !== this.$store.state.newsItems.length - 1 ? ++currentIndex : 0]

      return next.title
    },
    previousItem: function () {
      var selectedId = this.selectedItem.id

      var currentIndex = this.$store.state.newsItems.findIndex(function (el) {
        return el.id === selectedId
      })

      var prev = this.$store.state.newsItems[currentIndex !== 0 ? --currentIndex : this.$store.state.newsItems.length - 1]

      return prev.title
    }
  },
  methods: {
    backgroundImage: function () {
      return 'background-image: url("assets/' + this.selectedItem.folder + '/' + this.selectedItem.photos[this.selected] + '");'
    },
    reset: function () {
      this.$store.commit('clearSelected')
    },
    next: function () {
      this.$store.commit('nextSelected')
    },
    previous: function () {
      this.$store.commit('prevSelected')
    }
  }
}
</script>

<style lang="scss">
    @import '../styles/theme.scss';

    .selected-item {
        .header {
            background-color: $primaryColour;
            padding-top: 5px;
            padding-left: 15px;
            padding-bottom: 10px;
            border-bottom: 1px solid $secondaryColour;
            display: flex;
            flex-direction: row;
            justify-content: center;

            .nav-holder {
              margin-left: auto;
              display: flex;
              flex-direction: row;
              justify-content: center;

              .item-flip {
                font-size: 1.17em;
                margin-block-start: 1em;
                margin-block-end: 1em;
                margin-inline-start: 0px;
                margin-inline-end: 0px;
                margin-left: 15px;
                margin-right: 15px;
                font-weight: bold;
                cursor: pointer;
              }
            }

            .close {
              font-size: 1.17em;
              margin-block-start: 1em;
              margin-block-end: 1em;
              margin-inline-start: 0px;
              margin-inline-end: 0px;
              margin-left: auto;
              margin-right: 15px;
              font-weight: bold;
              cursor: pointer;
            }
        }

        .body {
            padding:15px;
            height: 100%;
            display: flex;
            flex-direction: column;

            .focus-photo {
                flex: 1 1 auto;
                width: 100%;
                margin-left: auto;
                margin-right: auto;
                background-size: contain;
                background-repeat: no-repeat;
                background-position: center center;
            }

            .photo-carousel {
                display: flex;
                flex-direction: row;
                margin-top: 15px;
                flex: 0 1 auto;

                .photo {
                    height: 50px;
                    width: 50px;
                    margin-left: 10px;
                    background-size: contain;
                    background-repeat: no-repeat;
                    background-position: center center;
                }
            }
        }
    }
</style>



